
.profile-page {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.subtitle {
  margin-left: auto;
  font-size: 0.75em;
  color: var(--apt-text-secondary);
  opacity: 0.7;
  font-weight: 400;
}

.apt-card-body {
  flex: 1;
  overflow-y: auto;
}

.tenants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
  padding: 4px;
}

.tenant-profile-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--apt-border);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 107, 157, 0.1);
    border-color: var(--apt-primary);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(255, 107, 157, 0.2);
  }
}

.tenant-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--apt-primary), var(--apt-secondary));
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.avatar-icon {
  font-size: 2em;
}

.tenant-basic-info {
  flex: 1;
  min-width: 0;
}

.tenant-name {
  margin: 0 0 8px 0;
  font-size: 1.1em;
  font-weight: 700;
  color: var(--apt-text);
}

.tenant-meta {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85em;
  color: var(--apt-text-secondary);
}

.meta-icon {
  font-size: 1.1em;
}

// 详情弹窗样式（复用 RoomDetailModal 的样式）
.detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.detail-panel {
  background: var(--apt-card);
  border: 2px solid var(--apt-border);
  border-radius: 16px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.detail-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid var(--apt-border);
  background: rgba(255, 107, 157, 0.1);
}

.detail-title {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0;
  font-size: 1.5em;
  font-weight: 700;
  color: var(--apt-text);
}

.detail-icon {
  font-size: 1.2em;
}

.close-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid var(--apt-border);
  border-radius: 8px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--apt-text);
  font-size: 1.5em;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 107, 157, 0.2);
    border-color: var(--apt-primary);
  }
}

.detail-body {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.tenant-details {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.detail-section {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
}

.section-title {
  margin: 0 0 16px 0;
  font-size: 1.1em;
  font-weight: 600;
  color: var(--apt-primary);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.info-label {
  font-size: 0.85em;
  color: var(--apt-text-secondary);
  opacity: 0.8;
}

.info-value {
  font-size: 1em;
  font-weight: 600;
  color: var(--apt-text);

  &.highlight {
    color: var(--apt-primary);
  }
}

.memories-list,
.schedule-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.memory-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
}

.memory-icon {
  font-size: 1.2em;
  flex-shrink: 0;
}

.memory-text {
  flex: 1;
  line-height: 1.5;
  color: var(--apt-text);
}

.schedule-item {
  display: grid;
  grid-template-columns: 80px 1fr auto;
  gap: 12px;
  align-items: center;
  padding: 10px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
}

.schedule-time {
  font-weight: 700;
  color: var(--apt-primary);
  font-size: 0.9em;
}

.schedule-activity {
  color: var(--apt-text);
  font-weight: 600;
}

.schedule-location {
  font-size: 0.85em;
  color: var(--apt-text-secondary);
  opacity: 0.8;
}

.empty-message {
  text-align: center;
  padding: 20px;
  color: var(--apt-text-secondary);
  opacity: 0.6;
  font-style: italic;
}

.detail-footer {
  display: flex;
  gap: 12px;
  padding: 20px 24px;
  border-top: 1px solid var(--apt-border);
  background: rgba(0, 0, 0, 0.2);
}
