
/* 導入全局主題樣式 */
@import './styles/theme.scss';

#app-container {
  width: 100%;
  height: 100%;
  min-width: 800px;
  min-height: 600px;
  color: var(--apt-text);
  background-color: var(--apt-bg);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  padding: 16px;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* 主卡片容器 */
.main-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  max-width: 1400px;
  max-height: 900px;
  margin: 0 auto;
}

/* 头部信息栏 */
.header-bar {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 12px 20px;
  background: linear-gradient(135deg, #ff6b9d, #c44569);
  border-bottom: none;
  flex-shrink: 0;
  border-radius: 12px 12px 0 0;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 8px;
}

.title-icon {
  font-size: 1.5em;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.title-text {
  font-size: 1.3em;
  font-weight: 700;
  color: #ffffff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.header-info-line {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.95);
  margin-left: 32px;
}

.info-item {
  white-space: nowrap;
}

.info-separator {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 300;
}

/* 响应式调整 */
@media (max-width: 600px) {
  .header-bar {
    padding: 10px 16px;
  }

  .title-icon {
    font-size: 1.2em;
  }

  .title-text {
    font-size: 1.1em;
  }

  .header-info-line {
    font-size: 0.8em;
    margin-left: 28px;
    flex-wrap: wrap;
  }
}

/* 标签页内容区域 */
.tab-content {
  flex: 1;
  overflow: hidden;
  padding: 16px;
  box-sizing: border-box;
}

.tab-pane {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}

/* 底部标签页按钮栏 - 融合在卡片内 */
.tab-buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  border-top: 1px solid var(--apt-border);
  padding: 12px 16px;
  gap: 8px;
}

.tab-btn {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid transparent;
  border-radius: 8px;
  color: var(--apt-text-secondary);
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
  font-weight: 500;
  min-width: 100px;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 107, 157, 0.5);
    color: var(--apt-text);
  }

  &.active {
    background: linear-gradient(135deg, var(--apt-primary) 0%, var(--apt-secondary) 100%);
    border-color: var(--apt-primary);
    color: #ffffff;
    box-shadow: 0 2px 8px rgba(255, 107, 157, 0.4);

    .tab-icon {
      transform: scale(1.1);
    }
  }

  &:active {
    transform: scale(0.98);
  }
}

.tab-icon {
  font-size: 18px;
  transition: transform 0.2s ease;
}

.tab-label {
  font-size: 14px;
  white-space: nowrap;
  font-weight: 600;
}

/* 响应式设计 */
@media (max-width: 600px) {
  .tab-btn {
    min-width: 70px;
    padding: 8px 12px;
    gap: 6px;
  }

  .tab-icon {
    font-size: 16px;
  }

  .tab-label {
    font-size: 12px;
  }
}
