
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: var(--apt-card);
  border: 2px solid var(--apt-border);
  border-radius: 16px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid var(--apt-border);
  background: rgba(255, 107, 157, 0.1);
}

.modal-title {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0;
  font-size: 1.5em;
  font-weight: 700;
  color: var(--apt-text);
}

.room-icon {
  font-size: 1.2em;
}

.close-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid var(--apt-border);
  border-radius: 8px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--apt-text);
  font-size: 1.5em;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 107, 157, 0.2);
    border-color: var(--apt-primary);
  }
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.room-details {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.detail-section {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
}

.section-title {
  margin: 0 0 16px 0;
  font-size: 1.1em;
  font-weight: 600;
  color: var(--apt-primary);
  display: flex;
  align-items: center;
  gap: 8px;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 4px;

  &.full-width {
    grid-column: 1 / -1;
  }
}

.info-label {
  font-size: 0.85em;
  color: var(--apt-text-secondary);
  opacity: 0.8;
}

.info-value {
  font-size: 1em;
  font-weight: 600;
  color: var(--apt-text);

  &.highlight {
    color: var(--apt-primary);
  }
}

.occupants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 12px;
}

.occupant-card {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  background: rgba(255, 107, 157, 0.1);
  border: 1px solid rgba(255, 107, 157, 0.3);
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.2s;

  &.clickable {
    cursor: pointer;

    &:hover {
      background: rgba(255, 107, 157, 0.2);
      border-color: var(--apt-primary);
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
    }
  }
}

.occupant-icon {
  font-size: 1.3em;
}

.occupant-name {
  font-size: 0.95em;
}

.empty-message {
  text-align: center;
  padding: 20px;
  color: var(--apt-text-secondary);
  opacity: 0.6;
  font-style: italic;
}

.room-description {
  margin: 0;
  line-height: 1.6;
  color: var(--apt-text);
  opacity: 0.9;
}

.modal-footer {
  display: flex;
  gap: 12px;
  padding: 20px 24px;
  border-top: 1px solid var(--apt-border);
  background: rgba(0, 0, 0, 0.2);
}

// 互动面板样式
.interaction-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.2s ease;
}

.interaction-panel {
  background: var(--apt-card);
  border: 2px solid var(--apt-primary);
  border-radius: 16px;
  width: 90%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(255, 107, 157, 0.5);
  animation: slideUp 0.3s ease;
}

.interaction-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid var(--apt-border);
  background: rgba(255, 107, 157, 0.15);
}

.interaction-title {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0;
  font-size: 1.3em;
  font-weight: 700;
  color: var(--apt-text);
}

.interaction-icon {
  font-size: 1.2em;
}

.interaction-body {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.interaction-hint {
  text-align: center;
  color: var(--apt-text-secondary);
  opacity: 0.7;
  font-style: italic;
  margin: 0;
}

.interaction-actions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 12px;

  .apt-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 16px 12px;
    font-size: 0.95em;

    span:first-child {
      font-size: 1.8em;
    }
  }
}

.interaction-footer {
  padding: 16px 24px;
  border-top: 1px solid var(--apt-border);
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
}
