
.map-view {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.subtitle {
  margin-left: auto;
  font-size: 0.75em;
  color: var(--apt-text-secondary);
  opacity: 0.7;
  font-weight: 400;
}

.apt-card-body {
  flex: 1;
  overflow-y: auto;
}

.floor-level {
  background: var(--apt-card);
  border: 1px solid var(--apt-border);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.floor-title {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--apt-text);
  display: flex;
  align-items: center;
  gap: 8px;

  &::before {
    content: '🏢';
  }
}

.floor-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  width: 100%;
}

.room-card {
  border-radius: 10px;
  padding: 10px;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  gap: 8px;
  font-size: 0.85em;
  border: 2px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: all 0.2s;
  position: relative;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(255, 107, 157, 0.3);
    border-color: var(--apt-primary);
  }

  // 合并房间占据两格宽度
  &.double-width {
    grid-column: span 2;
  }

  // 左侧：房间基础信息
  .room-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 6px;
    text-align: left;
    min-width: 0; // 允许文字截断
  }

  .room-id {
    font-weight: 700;
    font-size: 1.1em;
    color: var(--apt-text);
    opacity: 0.9;
  }

  .room-name {
    font-weight: 600;
    font-size: 0.95em;
    color: var(--apt-text-secondary);
  }

  // 右侧：当前在这里的人
  .room-occupants {
    flex-shrink: 0;
    width: 80px; // 从 60px 增加到 80px，确保能显示四字人名
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid rgba(255, 255, 255, 0.2);
    padding-left: 8px;
  }

  .occupants-summary {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 4px;
    padding: 8px 4px;
    background: rgba(255, 107, 157, 0.2);
    border-radius: 6px;
    text-align: center;
    width: 100%;
  }

  .summary-icon {
    font-size: 1.5em;
  }

  .summary-text {
    font-size: 0.75em;
    font-weight: 600;
    color: var(--apt-text);
  }

  .occupants-list {
    display: flex;
    flex-direction: column;
    gap: 4px;
    width: 100%;
  }

  .person-indicator {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 0.8em; // 从 0.75em 增加到 0.8em
    background: rgba(255, 255, 255, 0.1);
    padding: 3px 5px; // 从 2px 4px 增加到 3px 5px
    border-radius: 4px;
    white-space: nowrap;
  }

  .person-icon {
    flex-shrink: 0;
    font-size: 1.2em;
  }

  .person-name {
    flex: 1;
    min-width: 0;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .empty-indicator {
    font-size: 0.75em;
    color: var(--apt-text-secondary);
    opacity: 0.5;
  }

  // 房间类型样式
  &.bedroom {
    background: var(--room-bedroom);
  }

  &.bedroom.vacant {
    background: var(--room-bedroom-vacant);
    border-color: rgba(255, 182, 193, 0.8);
  }

  &.empty {
    background: var(--room-empty);
    border-color: #5a5f6b;
    opacity: 0.7;
  }

  &.your {
    background: var(--room-player);
    border-color: #5dade2;
  }

  &.public-lounge {
    background: var(--room-lounge);
    border-color: #fbbf24;
  }

  &.public-gym {
    background: var(--room-gym);
    border-color: #34d399;
  }

  &.public-laundry {
    background: var(--room-laundry);
    border-color: #38bdf8;
  }

  &.custom-functional {
    background: var(--room-custom);
    border-color: rgba(52, 211, 153, 0.8);
  }
}
